.dm_card {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: white;
  width: 100%;
  min-height: 66px;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  border-radius: 6px;
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  color: #1c023b;
  gap: 1vw;
  width: 100%;
  cursor: pointer;
  padding: 0 1rem;
  margin-top: 0.7rem;
}
.dm_card_inner {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.dm_card_inner > h6 {
  font-weight: 600;
  font-size: 0.9vw;
  line-height: 20px;
  color: #1c023b;
  margin: 0;
}

.dm_card_inner > p {
  font-weight: 400;
  font-size: 0.75vw;
  line-height: 20px;
  color: rgba(18, 18, 18, 0.6);
  margin: 0;
}

.menu_icon {
  width: 1.5rem;
  cursor: pointer;
  position: absolute;
  right: 3%;
}

.dm_image {
  height: 2.7vw;
  width: 2.7vw;
  border-radius: 50%;
}
